body, div, p, h1, ul,{
  font-family: "明朝体";
  margin: 0;
  padding: 0;

}

body {
  margin: 50px 100px 100px 100px;
  background-image: url("./flag.jpg");
}


.main {
  text-align: center;
}


.title {
  color: #339299;
  margin-top: 100px;
  font-size: 50px;
  background: #333333;
  border-radius: 50px;
  width: 900px;
  margin-left: auto;
  margin-right: auto;
}

.title h1 {
  color: #FF8C00;
  background: -webkit-linear-gradient(0deg, #40E0D0, #FF8C00, #FF0080);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.selecter {
  color: #592755;
  margin-right: auto;
  margin-left: auto;
  background: #000000;
  border-radius: 50px;
  width: 300px;
}

.view {
  padding: 50px 0 50px 0;
  display: flex;
  margin-right: auto;
  margin-left: auto;
  margin-top: 70px;
  background: #333333;
  border-radius: 50px;
}


.view ul {
  list-style: none;
  text-align:center;
  margin-right: auto;
  margin-left: auto;
}

.view li {
  text-align: left;
  margin-right: auto;
  margin-left: auto;
  margin-top: 15px;
  font-size: 45px;
  padding: 5px 5px 5px 15px;
}

.view img {
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
  width: 40%;
  height: auto;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
